import RootRouter from './root'

class PageRouter extends RootRouter {
  constructor() {
    super()

    console.log("sssssssssssssssssssssssssssssssssssssssssss")
    this.router.get("/*", this.routeHome.bind(this))

    return this.router
  }

  routeHome(req, res) {
    res.send("home api")
  }

  static getPageRouter() {
    return new PageRouter()
  }
} 

module.exports = PageRouter.getPageRouter()
// export default PageRouter.getPageRouter()


